#cartContainer{
    position: relative;
    display: block;
#cart{
    right: 0;
    position: absolute;
    color: black;
    background-color: #f9f9f9;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 3;
    border-radius: 20px;
    padding: 10px;
    margin-top: 40px;
    border: 5px solid black;
    min-width: 200px;
    max-height: 300px;
    #cartItems{
        max-height: 150px;
        overflow-y: auto;
    }
    ::-webkit-scrollbar-thumb{
        background-color: red;
        border-radius: 999px;
    }
  button{
    width: 80%;
    margin-left: 20%;
    background-color: rgb(196, 14, 14);
    color: white;
    position: relative;
    &:hover{
        background-color: rgb(99, 7, 7);
    }
  }
  #cartSum{
    font-size: 15px;
    padding: 10px;
  }
}
}