header{
    position: fixed;
    z-index: 2;
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    font-weight: 700;
    font-size: 20px;
    background-color: black;
    #headerItems{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 80%;
        margin: auto;
    }
    img{
        width: 20px;
    }
    button{
        position: absolute;
        right: 10%;
        padding: 10px;
        height: 40px;
        border: none;
        border-radius: 5px;
        background-color: white;
        color: black;
        font-size: 15px;
        font-weight: 700;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .emptyCount{
        display: inline-block;
        background-color: rgb(116, 116, 116);
        border-radius: 999px;
        width: 20px;
        padding: 5px;
        margin: 10px;
        font-size: 15px;
        text-align: center;
    }
    .notEmptyCount{
        background-color: red;
    }
}